.wrap{
    display: flex;
    align-items: center;
    margin-top: 30px;
    span{
        display: inline-block;
        margin: 0px 5px;
        font-size: 20px;
        color:rgb(255, 102, 1);
        text-align: center;
        line-height: 48px;
        font-weight: 400;
    }
    
    svg, span{
        width: 50px;
        height: 50px;
        background-color: #fff;
        border: 1px solid rgb(255, 102, 1);
        border-radius: 50%;
        cursor: pointer;
        &:hover{
            background-color: rgb(255, 102, 1);
            color: #fff;
            stroke: rgb(255, 255, 255);

        }
    }
    svg{
        margin: 0px 5px;
        stroke: rgb(255, 102, 1);
    }
    .active{
        background-color: rgb(255, 102, 1); 
        color: #fff;
    }
}